.cardContainer {
    max-width: 800px;
    margin-top: 15px;
    .dateText {
        font-size: 12px;;
        display: flex;
        justify-content: flex-end;
        margin-bottom: 5px;;
    }
    .card {
        .personalInfoRight {
            display: flex;
            flex: 0 0 45%;
            .cardImg {
                position: relative;
            img {   
                width: 133px;;
                margin-left: 12px;
                border-radius: 5px;
                cursor: pointer;
            }
            .rating {
                position: absolute;
                bottom: -15px;
                right: 14px;;
            }
        }
            .personalDetailsRight {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
            }
        }
        .cardContent {
            font-size: 14px;;
        }
        .personalInfoLeft {
            flex: 0 0 55%;
            border-right: 1pt solid rgba(0,0,0,.18);
            padding-right: 12px;
                .mySelfDescBox {
                    margin-top:21px;
                    & > span {
                        height:74px;
                        display:block;
                        overflow:hidden;
                    }
                }
                .workDays {
                    margin-top: 7px;
                }
        }


        .cardActions {
            justify-content: flex-end;
        }
    }
}