.containerDetailsPerson {
    display: flex;
    flex-direction: column;
    .card {
        max-width: 700px;
        font-size: 14px;;
        margin-left: 24px;
        .cardContent {
            & > *:not(:last-child) {
                margin-bottom: 18px;
            }
            .title {
                color: #120639;
                font-size: 24px;;
                font-style: italic;
            }

            .personalDetails {
                width: 550px;;
                    & + .boxImage {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        & > img {
                            width: 160px;;
                            height: 160px;
                            border-radius: 5px;
                        }
                        button {
                            border: 0;
                            outline: none;
                            background: inherit;
                            font-size: 12px;
                            cursor: pointer;
                            text-decoration: underline;
                            opacity: .54;
                            transition: .2s;
                            &:hover {
                                    opacity: 1;
                            }
                            &:active {
                                transform: scale(.9);
                            }
                        }
                    }
            }
            .fieldRow {
                display: flex;
                justify-content: space-between;
                width: 100%;
                margin-bottom: 12px;
                & > span:first-child {
                    color: #120639;
                    font-weight: 700;
                    flex: 0 0 180px;
                }
                & > span:nth-child(2) {
                    flex: 1;
                }
                & > span:last-child {
                    flex: 1;
                }
            }
            .descriptionExperienceBox {
                width: 60%;
            }
        }
    }
    .commentsContainer {
        flex: 1;
    }
}
.containerSpinner {
    height: calc(100vh - 42px);
    display: flex;
    align-items: center;
    justify-content: center;;
}

a {
    text-decoration: none;
}