.boxPage {
    background-image: linear-gradient(rgba(0, 0, 0, 0.9),rgba(0, 0, 0, 0.9)),
    url('https://img.freepik.com/free-photo/woman-children-sitting-floor_23-2147663975.jpg?size=626&ext=jpg');
    height: calc(100vh - 42px);
    background-repeat: no-repeat;
    background-size: cover;
}
.paperLogin {
    padding: 16px;;
    min-width: 360px;;
    max-width: 360px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 auto;
    height: 220px;
}

.inputStyle {
    input {
        font-size: 14px;
    }
}

.sectionBottom {
    position: relative;
    display: flex;
    flex-direction: column;
    .btnSubmit {
        position: relative;
        .circularPrrogress {
            position: absolute;
            left: 10px;
            color: #fff;
        }
    }
    .incorrectInfo {
        font-size: 10px;
        color: #FF9494;
        position: absolute;
        top: -17px;
        left: 0;
    }
}