.container {
    background-image: linear-gradient(rgba(0, 0, 0, 0.9),rgba(0, 0, 0, 0.9)), 
    url('http://cachevalleyfamilymagazine.com/wp-content/uploads/2015/04/babysitting.jpg');
    height: calc(100vh - 42px);
    padding-top: 18px;
    background-size: cover;
    background-attachment:fixed;
}

.main {
    // height: calc(50vh - (63.2px / 2));
    color: #fff;
    & > div:first-child {
        & > h2 {
                line-height: 70px;;
            }
        
    }
}

