
.dialogTitle {
    text-align: center; 
}

.rating {
    direction: ltr;
}
.dialogContainer {
    .topSection {
        display: flex;
        justify-content: flex-end;
        padding: 0 12px;
    }
    .containerContent {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
            .dialogContentRate {
                display: flex;
                flex-direction: column;
                align-items: center;
                height: 80px;
            }
    }
        .contentTextArea {
            width: 100%;;
            display: flex;
            flex-direction: column;
            align-items: center;
            .textAreaReview {
                height: 150px;
                width: 100%;
                resize: none;
                padding: 12px;;
                font-size:16px;
                margin-bottom: 24px;
            }
    }
}


