*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;;
}

/* GLOBALS */

.container {
  max-width: 1024px;;
  margin: 0 auto;;
}

.text-opacity {
  opacity: .54;
}


/* HELPERS */
 
.flex {
  display: flex;;
}

.column {
  flex-direction: column;
}

.justify-content-center {
  justify-content: center;
}

.space-between {
  justify-content: space-between;
}

.space-around {
  justify-content: space-around;
}

.align-items-center {
  align-items: center;
} 

.full-width {
  width: 100%;
}

.text-align-center {
  text-align: center;
}

.bold {
  font-weight: bold;
}

.black-light {
  color: rgba(0,0,0,.54);
}

.icon-flipped {
  transform: scaleX(-1);
  -moz-transform: scaleX(-1);
  -webkit-transform: scaleX(-1);
  -ms-transform: scaleX(-1);
}


/*  */

/* AUTOCOMPLETE */

.autocmplete-style-dialog {
  width: 70%;
}

.navbar-app {
  height: 42px;
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,.54);
}


ul.pagination {
  list-style-type: none;
  display: flex;
  justify-content: space-evenly;
  max-width: 800px;
  align-items: flex-end;
  color: #005f8e;
  display: flex;
  align-items: center;
}

ul.pagination li {
  padding: 6px 12px;
  font-weight: 600;
  cursor: pointer;
  transition: .2s;;
  margin: 18px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

ul.pagination li:hover {
  opacity: .54;
}

ul.pagination li a {
  display: flex;
  align-items: center;
}

ul.pagination .active {
  /* font-weight: 600; */
  color: #00a5f5;
  border: 2px solid rgba(0,0,0,.1);
  border-radius: 5px;
}

.MuiButton-containedSecondary {
  color: #fff !important;
  background-color: #1976d2 !important;
}

.addField {
  width: 100%;;
}